
  import { defineComponent, PropType } from 'vue';
  
  export default defineComponent({
    name: 'MainNavbar',
    props: {
      pages: {
        type: Array as PropType<{ link: { text: string; url: string }; }[]>,
        required: true
      },
      activePage: {
        type: Number,
        required: true
      },
      navLinkClick: {
        type: Function as PropType<(index: number) => void>,
        required: true
      }
    },
    data() {
      return {
        theme: 'light',
      };
    },
    methods: {
      changeTheme() {
        let theme = 'light';
  
        if (this.theme === 'light') {
          theme = 'dark';
        }
  
        this.theme = theme;
      }
    }
  });
  